.help {
  width: 100%;
  padding-top: 0.3rem;
  word-break: break-all;
  // overflow: auto;
  .help_top {
    // padding: 40px 20px 0 60px;
    width: 80%;
    height: 2.34rem;
    box-shadow: 0 0 0.2rem 0.1rem rgba(105, 106, 230, 0.05);
    background: rgba(255, 255, 255, 0.9);
    border-radius: 0.12rem;
    position: relative;
    .bg_right {
      width: 40%;
      height: 67%;
      background: url(../../../assets/images/bg-help.png);
      background-repeat: no-repeat;
      background-size: 100% 100%;
      position: absolute;
      right: 0;
      bottom: 0.2rem;
    }
    .help_title {
      font-size: 0.22rem;
      font-weight: bold;
      line-height: 0.28rem;
      color: #040e34;
      margin-top: 0.4rem;
      margin-left: 0.6rem;
    }
    .help_searh {
      width: 4rem;
      height: 0.38rem;
      line-height: 0.38rem;
      border: 1px solid #d9dbe1;
      margin: 0.3rem 0;
      margin-left: 0.6rem;
      padding-left: 0.1rem;
      img {
        width: 0.2rem;
        height: 0.2rem;
      }
    }
    .help_tabs {
      padding-top: 0.2rem;
      .ant-tabs {
        color: #9696a3;
      }
      .ant-tabs-nav-list {
        margin-left: 0.6rem;
        .ant-tabs-tab {
          padding: 0.12rem 0;
          font-size: 0.14rem;
        }
      }
      .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
        color: #5c5ee4;
      }
      .ant-tabs-nav::before {
        border: none;
      }
      .ant-tabs-tab-active:after {
        content: "";
        height: 2px;
        width: 10px;
        background-color: #5c5ee4;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%);
      }
      .ant-tabs-ink-bar {
        display: none;
      }
      .ant-tabs-nav {
        margin: 0;
      }
      .usual {
        flex-wrap: wrap;
        // justify-content: space-between;
        margin-top: 0.4rem;
        width: 100%;
        .pro_itemright {
          margin-left: 0.2rem;
          width: 75%;
        }
        .pro_item {
          // width: 31%;
          height: 2rem;
          padding-top: 0.5rem;
          padding-left: 0.4rem;
          background-color: rgba(255, 255, 255, 0.9);
          border-radius: 0.12rem;
          margin-bottom: 0.3rem;
          img {
            width: 0.48rem;
            height: 0.48rem;
          }
          .pro_itemname {
            font-size: 0.18rem;
            font-weight: 500;
            color: #040e34;
            line-height: 0.4rem;
          }
          .pro_itemcontent {
            font-size: 0.14rem;
            line-height: 0.28rem;
            cursor: pointer;
            width: 90%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          .pro_itemcontent:hover {
            color: #5c5ee4;
          }
        }
      }
      .total {
        margin-top: 0.4rem;
        justify-content: space-between;
        .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
          background-color: #f0f0fd;
          color: #5c5ee4;
        }
        .ant-menu-inline .ant-menu-item::after {
          // position: absolute;
          top: 30%;
          left: 0;
          bottom: 30%;
          right: auto;
          border-right: 3px solid #5c5ee4;
        }
        .ant-menu-submenu-arrow::before,
        .ant-menu-submenu-arrow::after {
          background-color: #525666;
        }
        .ant-menu-item:active,
        .ant-menu-submenu-title:active {
          background-color: #f0f0fd;
        }
        .ant-menu-sub.ant-menu-inline {
          background-color: #fff;
          padding: 0 0.2rem;
        }
        .ant-menu-submenu-selected {
          color: #5c5ee4;
        }
        .ant-menu-submenu-title:hover {
          color: #5c5ee4;
        }
        .ant-menu-item:hover {
          color: #5c5ee4;
        }
        .ant-menu-sub.ant-menu-inline > .ant-menu-item,
        .ant-menu-sub.ant-menu-inline
          > .ant-menu-submenu
          > .ant-menu-submenu-title {
          height: 0.36rem;
          line-height: 0.36rem;
        }
        .left_menu {
          background: rgba(255, 255, 255, 0.9);
          border-radius: 0.12rem;
          // width: 3rem;
          // margin-right: 0.3rem;
          .menu_top {
            font-size: 0.2rem;
            font-weight: 400;
            line-height: 0.7rem;
            padding-top: 0.1rem;
            color: #040e34;
            padding-left: 0.3rem;
            border-bottom: 0.01rem solid #e8ecf7;
          }
          .menu_title {
            padding: 0.25rem 0;
          }
        }
        .right_content {
          background: rgba(255, 255, 255, 0.9);
          border-radius: 0.12rem;
          height: 100%;
          // flex: 1;
          padding: 0.2rem;
          position: relative;
          overflow: auto;
          .detialtitle {
            font-size: 0.32rem;
            font-weight: bold;
            line-height: 0.5rem;
            color: #040e34;
            text-align: center;
          }
          .subtimebar {
            justify-content: space-evenly;
            padding: 0 0.3rem;
            margin-bottom: 0.2rem;
            .keywords {
              max-width: 50%;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
          .download {
            color: #1890ff;
            cursor: pointer;
          }
          .download:hover {
            opacity: 0.7;
          }
          .contents {
            word-break: break-all;
            margin-bottom: 1rem;
          }
          .attachment_box {
            position: absolute;
            bottom: 0.2rem;
          }
        }
      }
    }
  }
}

@primary-color: #5C5EE4;